/**********************
      Navigation      
**********************/

.notes-nav {
  display: flex;
  flex-shrink: 0;
  justify-content: space-between;
  align-items: stretch;
  padding: 10px;
  background: #f7f9fa;
  box-shadow: 0px 1px 2px 0px darkgrey;
  border-bottom: 1px solid #7f82af;
}

.notes-nav > span {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 22px;
  margin-bottom: 0px;
  width: 36px;
}

.notes-nav .filter-select {
  flex:1;
  padding: 10px;
  padding: 0 10px;
  display: flex;
}

.notes-nav .filter-select .filter-tags {
  min-width: 100%;
}
.notes-nav .filter-select .ant-select-selector{
  border-radius: 5px;
  padding-right: 55px;
  height: 100%;
}

.notes-nav .filter-select .anticon-search{
  position: relative;
  left: -36px;
  pointer-events: none;
  padding: 0 7px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 22px;
  margin-bottom: 0px;
  width: 36px;
  background: lightgray;
  border-radius: 0 4px 4px 0;
}

.notes-nav .filter-select .ant-select-clear{
  top: 45%;
  right: 45px;
  font-size: 16px;
}

/**********************
      Notes Stuff      
**********************/

.notes-shell{
  min-height: 100vh;
  flex-flow: column;
  display: flex;
}

.notes-shell .notes-content-wrapper {
  flex-grow: 1;
}

.notes-shell .lecture-details {
  padding : 14px 10px;
  border-bottom: 1px solid #7b795f;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.notes-shell .lecture-details .lecture-title{
  font-size: 18px;
  font-weight: 400;
  margin-bottom: 5px;
}

.notes-shell .lecture-details .lecture-course{
  font-size: 16px;
  font-weight: 300;
  color: #1890ff;
  display: inline-flex;
  align-items: center;
}

.notes-shell .lecture-details .lecture-course b{
  margin: 0 6px;
}

.notes-shell .lecture-details .anticon {
  font-size: 20px;
  padding: 0 10px;
  cursor: pointer;
}

.notes-shell .search-results {
  margin: 15px 5px;
  font-size: 16px;
  background: ghostwhite;
  padding: 7px;
  border: 2px solid #79794f;
}

.notes-shell .search-results .s-tag,
.videos-shell .search-results .s-tag {
  padding: 1px 4px;
  border: 1px solid goldenrod;
  background: #fff062;
  margin-right: 7px;
  border-radius: 5px;
  font-size: 14px;
  display: inline-block;
  margin-top: 7px;
}

.notes-shell .notes-container {
  padding: 10px 5px;
}

.notes-shell .note-item,
.videos-shell .note-item {
  padding: 10px;
  margin-bottom: 1.75em;
  border: 1px solid darkgrey;
  background: white;
  box-shadow: 0px 0px 6px -2px rgb(0 0 0 / 30%);
  border-radius: 7px;
}
.note-item .note-top {
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.note-item .note-ts {
  background: #f1f1f1;
  padding: 1px 4px;
  font-weight: 400;
  font-size: 15px;
  border-radius: 4px;
  border: 1px solid darkgray;
  color: #1890ff;
  cursor: pointer;
}

.note-item .note-edit .anticon{
  font-size: 18px;
  padding: 5px;
  cursor: pointer;
}

.note-item .note-content {
  margin: 10px 2px;
}

.note-item .note-tags {
}
.note-item .note-tag {
  display: inline-block;
  margin-right: 10px;
  background: #fff062;
  padding: 1px 5px;
  font-weight: 400;
  font-size: 12px;
  border-radius: 5px;
  border: 1px solid goldenrod;
}

/* EDITING stuff */

.note-item .note-top-edit {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}
.note-item .note-top-edit .ant-picker{
  max-width: 80px;
  padding-left: 7px;
  padding-right: 7px;
}

.note-item .note-top-edit .ant-picker .ant-picker-suffix,
.note-item .note-top-edit .ant-picker .ant-picker-clear{
  display: none;
}


.note-item .note-content-edit {
  margin-bottom: 15px;
}

.note-item .note-tags-edit {
  display: flex;
  align-items: center;
}
.note-item .note-tags-edit .ant-select {
  min-width: 120px;
}
.note-item .note-tags-edit .ant-select-selector {
  padding-right: 22px;
}
.note-item .note-tags-edit .anticon.anticon-plus {
  position: relative;
  left: -22px;
  pointer-events: none;
}


.lecture-edit-modal .label{
  margin-bottom: 7px;
}

.lecture-edit-modal .title-input{
  margin-bottom: 20px;
}


.notes-shell .notes-footer {
  background: #3f3f3f;
  flex-shrink: 0;
  color: white;
  position: sticky;
  bottom: 0px;
  padding: 7px 14px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.notes-shell .notes-footer .anticon {
  font-size: 22px;
  color: white;
  cursor: pointer;
}


/*********************
      VideoPlayer     
**********************/

.video-player {
  width: 100%;
  z-index: 999;
  flex-shrink: 0;
}

.video-player.pinned {
  position: sticky;
  top: 0;
}

.notes-shell .video-container {
  background: black;
  overflow: hidden;
  overflow-x: scroll;
  line-height: 0;
}

.notes-shell .video-container .ant-spin {
  /* margin: auto;
  width: 100%;
  height: 100%;
  padding-top: 20%; */
  display: flex;
  flex-flow: column;
  height: 100%;
  align-items: center;
  justify-content: center;
  font-size: 16px;
}

.notes-shell .video-container .ant-spin .ant-spin-dot{
  font-size: 34px;
  margin: 20px;
}

.notes-shell .video-container video {
  max-width: 100%;
  /* max-height: 100%; */
  display: block;
  margin: auto;
}